
import React,{Component}from "react";
import axios from 'axios';
import '../App.css';
import Moment from 'react-moment';
import 'moment-timezone';
import Clock from 'react-live-clock';
import Icon from "./Icon";

const Time = () => {
  let currentDate = Date.now();
  return (<span>{currentDate}</span>);
}

class CurrentWeather extends Component {
  constructor(props) {
    super(props);
    this.state = {
      givenLocation: "Miami, US",
    };
  }

  fetchData = () => {
    const url = `https://api.openweathermap.org/data/2.5/weather?q=${this.state.givenLocation}&APPID=b292bd2a578c27b2e97bbb03b7515a95&units=metric`;
   
    axios.get(url).then(response => {
      this.setState({
        data: response.data
      });
      
      // console.log(this.state.data);


      this.setState({
        city: this.state.data.name,
        country: this.state.data.sys.country,
        date: this.state.data.dt,
        temperature: Math.round(this.state.data.main.temp),
        max_temperature: Math.round(this.state.data.main.temp_max),
        min_temperature: Math.round(this.state.data.main.temp_min),
        description: this.state.data.weather[0].description,
        wind: this.state.data.wind.speed,
        humidity: this.state.data.main.humidity,
        pressure: this.state.data.main.pressure,
        icon: this.state.data.weather[0].icon,
        sunrise: this.state.data.sys.sunrise,
        sunset: this.state.data.sys.sunset,

      });
    });
  };

  componentDidMount() {
    this.fetchData();
  }

  newLocation = event => {
    event.preventDefault();
    const newLocationValue = this.newInputLocation.value;
    this.setState(
      {
        givenLocation: newLocationValue
      },
      () => {
        this.fetchData();
      }
    );
  };



  render(){
    const{city,country,temperature,max_temperature,min_temperature,icon,date,humidity,pressure,wind,description,givenLocation, sunrise,sunset}=this.state;
    return (
      <div className="main">
        <div><h1>Current Weather Update</h1></div>
        <div className="location"><strong><span>{city}, </span><span>{country}</span></strong></div>
        <div>
          <Moment format="ddd MMMM Do YYYY"><Time/></Moment>
          <p className="clock"><Clock format={'HH:mm:ss'} ticking={true} /></p>
        </div>
        <form className="form" onSubmit={this.newLocation}>
          <div>
          <span className="location-input">Enter Location : </span>
          <input className="input"
            defaultValue={givenLocation}
            type="text"
            ref={input => (this.newInputLocation = input)}
          />
          <button className="btn action">Get Weather</button>
          </div>
        </form>
        <hr/>
        <div className="current-day" key= {date}>
          <div className="details action" >
            <div className="current-day-temp"><b>Current Temperature: </b>{temperature}°C</div>
            <div><Icon icon={icon}/></div>
            <div className="datas">
              <div className="left">Max Temp</div><div className="middle">:</div><div className="right"> <b>{max_temperature}°C</b></div>
            </div>
            <div className="datas">
              <div className="left">Min Temp</div><div className="middle">:</div><div className="right"> <b>{min_temperature}°C</b></div>
            </div>
            <div className="datas">
              <div className="left">Humidity</div><div className="middle">:</div><div className="right"> <b>{humidity}%</b></div>
            </div>
            <div className="datas">
              <div className="left">Wind</div><div className="middle">:</div><div className="right"> <b>{wind} m/s</b></div>
            </div>
            <div className="datas">
              <div className="left">Pressure</div><div className="middle">:</div><div className="right"> <b>{pressure} hpa</b></div>
            </div>
            <div className="datas">
              <div className="left">Weather</div><div className="middle">:</div><div className="right"><span className="weather-description"><b>{description}</b></span></div>
            </div>
            {/* <div className="datas">
              <div className="left">Sunrise</div><div className="middle">:</div><div className="right"> <b><Moment format=" hh:mm:ss" >{sunrise}</Moment> AM</b></div>
            </div>
            <div className="datas">
              <div className="left">Sunset</div><div className="middle">:</div><div className="right"> <b><Moment format=" hh:mm:ss" >{sunset}</Moment> PM</b></div>
            </div> */}
          </div>
        </div><hr/>
        <div className="made-with-love">Made with <i>♥</i></div>
        <span className="made-with-love">Copyright © 2019 by Mirza. All Rights Reserved.</span>
      </div>
    );
  }
}
export default CurrentWeather;
